* {
  font-family: 'Lato', sans-serif;
}

#input-area {
  position: relative;
}
#search-field {
  height: 2rem;
  width: 25rem;
}
#suggestions-container {
  width: 25rem;
  border: 1px solid black;
  background-color: white;
  position: absolute;
  top: 2rem;
  left: 0;
}

#suggestions-container.hidden {
  display: none;
}

.country-container {
  border: 1px solid black;
}

img {
  max-width: 100px;
}

button {
  display: block;
}
